import React, { Fragment } from 'react';
import { useEffect } from 'react';
import { useState } from 'react';
import Header from './Components/OtherHeader/OtherHeader';
import axios from 'axios';
import Loading from './Components/Loading/Loading';
import ReloadError from './Components/ReloadError/ReloadError';


const TermsOfService = props => {

    const [terms, changeTerms] = useState(null);
    const [loading, changeLoading] = useState(false);
    const [error, changeError] = useState(false);

    const loadTerms = () => {
        changeLoading(true);
        changeError(false);
        axios.get('/api/terms/')
            .then(res => {
                changeLoading(false);
                changeTerms(res.data);
            })
            .catch(() => {
                changeLoading(false);
                changeError(true);
            })
    }

    useEffect(() => {
        loadTerms();
    }, [])

    let termsContent = null;

    if (loading) {
        termsContent = <Loading />
    } else if (terms) {
        termsContent = terms.terms_of_service
    } else if (error) {
        termsContent = <ReloadError />
    }

    return (
        <Fragment>
            <Header pageTitle="SOGO TV Terms of Service" />
            <div className="Container" dangerouslySetInnerHTML={{ __html: termsContent }} >
            </div>
        </Fragment>
    )
}

export default TermsOfService;
