import React, { Fragment, useEffect, useState } from 'react';
import Header from './Components/OtherHeader/OtherHeader';
import Product from './Components/Product/Product';
import axios from 'axios';
import Loading from './Components/Loading/Loading';
import ReloadError from './Components/ReloadError/ReloadError';
import { useDispatch } from "react-redux";
import { getCartProducts, updateCartId, updateCartQuantity } from './store/actions/cart';


const Shop = props => {

    const dispatch = useDispatch();

    const [shop, changeShop] = useState(null);
    const [loading, changeLoading] = useState(false);
    const [error, changeError] = useState(false);

    // cart states
    const [cartResponse, changeCartResponse] = useState(false);
    const [cartError, changeCartError] = useState(false);

    const loadshop = () => {
        changeLoading(true);
        changeError(false);
        axios.get('/api/products/')
            .then(res => {
                changeLoading(false);
                changeShop(res.data);
            })
            .catch(() => {
                changeLoading(false);
                changeError(true);
            })
    }

    useEffect(() => {
        loadshop();
    }, [])

    const showCartResponse = (error = false) => {
        changeError(error);
        changeCartResponse(true);
        setTimeout(() => {
            changeCartResponse(false);
            changeError(false);
        }, 3000);
    }

    async function createCart() {
        await axios.post('/api/cart/')
            .then(res => {
                localStorage.setItem('cartId', res.data.id);
                dispatch(updateCartId(res.data.id));
                // showCartResponse()
            })
            .catch(err => {
                showCartResponse(false);
            })
    }

    async function addProductToCart (id) {
        let cartId = localStorage.getItem('cartId');
        const data = {
            cart: parseInt(cartId),
            product: id,
            quantity: 1
        }
        await axios.post('/api/cart-products/', data)
            .then(res => {
                showCartResponse();
                dispatch(updateCartQuantity(1));
            })
            .catch(err => {
                showCartResponse(false);
            })
    }

    async function addToCart (id) {
        let cartId = localStorage.getItem('cartId');
        if (!cartId) {
            // create cart
            await createCart();
        }
        // add product to cart and update store reducer
        // console.log(cartId, localStorage.getItem('cartId'))
        await addProductToCart(id);
    }

    let shopContent = null;

    if (loading) {
        shopContent = <Loading />
    } else if (shop) {
        shopContent = shop.map(product => (
            <Fragment key={product.id} >
                <Product title={product.title} image={product.image}
                    price={product.price} id={product.id} addToCart={() => addToCart(product.id)} />
                <hr />
            </Fragment>
        ))
    } else if (error) {
        shopContent = <ReloadError />
    }

    return (
        <Fragment>
            <Header />
            <div className="Container">
                <div className="Shop">
                    {shopContent}
                </div>
            </div>
        </Fragment>
    )
}

export default Shop;