import React, { Fragment } from 'react';
import { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Link, withRouter } from 'react-router-dom';
import CartItem from './Components/CartItem/CartItem';
import Header from './Components/OtherHeader/OtherHeader';
import Payment from './Payment';
import { getCartProducts } from './store/actions/cart';


const Cart = props => {

    const dispatch = useDispatch();

    const cartProducts = useSelector(state => state.cart.cartProducts);

    const cartId = useSelector(state => state.cart.cartId);

    let cartItems = cartProducts.map(product => <CartItem key={product.id} data={product} />)
    
    const proceedToCheckout = e => {
        e.preventDefault();
        if (cartProducts.length > 0) {
            props.history.push('/checkout')
        }
    }

    useEffect(() => {
        dispatch(getCartProducts(cartId));
    }, [cartProducts.length])

    return (
        <Fragment>
            <Header />
            <div className="Container">
                <div className="CartItems">
                    <div className="CartFlex">
                        <p className="CartItem CartTitle">ITEM</p>
                        <p className="CartUnitPrice CartTitle">UNIT PRICE</p>
                        <p className="CartQuantity CartTitle">QUANTITY</p>
                        <p className="CartSubTotal CartTitle">SUBTOTAL</p>
                    </div>
                    <hr />
                    {cartItems}

                    {/*  */}
                    <div className="CartButtons">
                        <Link to='/shop' className="BackToShop">
                            <i className="fas fa-arrow-left"></i>
                            <span>Back to shop</span>
                        </Link>
                        <div onClick={proceedToCheckout}
                            style={{cursor: cartProducts.length>0?'pointer':'not-allowed'}}
                         className="CheckoutButton" >Proceed to checkout</div>
                    </div>
                </div>
            </div>
        </Fragment>
    )
}

export default withRouter(Cart);