import React from 'react';
import './Product.css';


const Product = props => {

    return (
        <div className="Product">
            <img src={props.image} alt="vr image"/>
            <div className="ProductContent">
                <p>{props.title}</p>
                <div className="PriceAddToCart">
                    <h2>${props.price}</h2>
                    <div className="ProductButton" onClick={() => props.addToCart(props.id)} >Add to cart</div>
                </div>
            </div>
        </div>
    )
}

export default Product;