import React from 'react';
import { PaystackButton } from 'react-paystack';
import './App.css';
import axios from 'axios';
import { withRouter } from 'react-router-dom';
import { useDispatch } from "react-redux";
import { getCartProducts } from './store/actions/cart';


function makeid(length) {
  var result = '';
  var characters = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
  var charactersLength = characters.length;
  for (var i = 0; i < length; i++) {
    result += characters.charAt(Math.floor(Math.random() * charactersLength));
  }
  return result;
}


function App(props) {
  const config = {
    reference: (new Date()).getTime() + makeid(20),
    email: props.email,
    amount: props.amount,
    publicKey: 'pk_test_08cd5a853ea5782862612e8eca0d112248bb5fe4',
  };

  const dispatch = useDispatch();

  // you can call this function anything
  const handlePaystackSuccessAction = (reference) => {
    // Implementation for whatever you want to do with reference and after success call.
    const data = {
      ref: reference.reference,
      email: props.email,
      name: props.name,
      street_name: props.streetName,
      city: props.city,
      country: props.country,
      zip_code: props.zipCode,
      phone_number: props.phoneNumber,
      cart_id: localStorage.getItem('cartId')
    }
    axios.post('/api/verify-payment/', data)
      .then(res => {
        localStorage.removeItem('cartId');
        props.history.replace('/shop')
        dispatch(getCartProducts());
      })
  };

  // you can call this function anything
  const handlePaystackCloseAction = () => {
    // implementation for  whatever you want to do when the Paystack dialog closed.
    console.log('closed')
  }

  const componentProps = {
    ...config,
    text: 'Make Payment',
    onSuccess: (reference) => handlePaystackSuccessAction(reference),
    onClose: handlePaystackCloseAction,
  };

  return (
    <PaystackButton {...componentProps} email={props.email} className={props.className} />
  );
}

export default withRouter(App);