import { call, put, takeEvery } from "redux-saga/effects";
import axios from 'axios';




function getCartProducts (cartId) {
    const id = cartId || null;
    const apiUrl = `/api/cart-products/?cart=${id}`
    return axios.get(apiUrl)
        .then(res => res.data)
        .catch(err => {throw err})
}

function* fetchCartProducts (payload) {
    try {
        const cartProducts = yield call(getCartProducts, payload.cartId);
        yield put({type: 'GET_CART_PRODUCTS_SUCCESS', cartProducts: cartProducts})
    } catch (err) {
        yield put({type: 'GET_CART_PRODUCTS_FAILED', error: err})
    }
}

function* cartSaga() {
    yield takeEvery('GET_CART_PRODUCTS_REQUESTED', fetchCartProducts)
}

export default cartSaga;