import React, { Fragment } from 'react';
import { useState } from 'react';
import { useEffect } from 'react';
import './CountDown.css';


const CountDown = props => {

    let futureDate = new Date(2020, 10, 1);

    futureDate = futureDate.valueOf();

    const [date, changeDate] = useState({
        days: 0,
        hours: 0,
        minutes: 0,
        seconds: 0
    })

    useEffect(() => {

        // Update the count down every 1 second
        const x = setInterval(function () {

            // Get today's date and time
            var now = new Date().getTime();

            // Find the distance between now and the count down date
            var distance = futureDate - now;

            // Time calculations for days, hours, minutes and seconds
            var days = Math.floor(distance / (1000 * 60 * 60 * 24));
            var hours = Math.floor((distance % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
            var minutes = Math.floor((distance % (1000 * 60 * 60)) / (1000 * 60));
            var seconds = Math.floor((distance % (1000 * 60)) / 1000);

            // Display the result in the element with id="demo"
            changeDate({
                days,
                hours,
                minutes,
                seconds
            })

            // If the count down is finished, write some text
            if (distance < 0) {
                clearInterval(x);
            }
        }, 1000);
    }, [])

    return (
        <Fragment>
            <h4 className="CountDownHeading">Available in</h4>
            <div className="CountDown">
                <div className="Days CountDownCard">
                    <div className="CountDownNumber">{date.days}</div>
                    <div className="CountDownLabel">Days</div>
                </div>
                <div className="Days CountDownCard">
                    <div className="CountDownNumber">{date.hours}</div>
                    <div className="CountDownLabel">Hours</div>
                </div>
                <div className="Days CountDownCard">
                    <div className="CountDownNumber">{date.minutes}</div>
                    <div className="CountDownLabel">Minutes</div>
                </div>
                <div className="Days CountDownCard">
                    <div className="CountDownNumber">{date.seconds}</div>
                    <div className="CountDownLabel">Seconds</div>
                </div>
            </div>
        </Fragment>
    )
}

export default CountDown;
