import React from 'react';
import { Link } from 'react-router-dom';
import './Footer.css';
import axios from 'axios';
import { useState } from 'react';
import { useEffect } from 'react';


const Footer = () => {

    const [footerData, changeFooterData] = useState({});

    useEffect(() => {
        axios.get('/api/footer/')
            .then(res => changeFooterData(res.data))
    }, [])

    return (
        <div className="Footer">
            <div className="FooterTop">
                <div className="FooterLeft">
                    <div className="FooterList">
                        <p className="FooterHeading">COMPANY</p>
                        <Link to='/about' className="FooterListItem">About Us</Link>
                        <Link to='/features' className="FooterListItem">Features</Link>
                        <Link to='/faq' className="FooterListItem">FAQ</Link>
                        <Link to='/shop' className="FooterListItem">Shop</Link>
                    </div>
                    <div className="FooterList">
                        <p className="FooterHeading">SOCIAL</p>
                        <a href={footerData.twitter_url} className="FooterListItem">Twitter</a>
                        <a href={footerData.facebook_url} className="FooterListItem">Facebook</a>
                        <a href={footerData.instagram_url} className="FooterListItem">Instagram</a>
                        <a href={footerData.linkedin_url} className="FooterListItem">LinkedIn</a>
                    </div>
                    <div className="FooterList">
                        <p className="FooterHeading">LEGAL</p>
                        <Link to='/privacy' className="FooterListItem">Privacy Policy</Link>
                        <Link to='/terms' className="FooterListItem">Terms of Service</Link>
                    </div>
                </div>
                <div className="FooterRight">
                    <p className="FooterRightHeading">SUBSCRIBE TO OUR NEWSLETTER</p>
                    <form className="SubscribeForm">
                        <input type="text" />
                        <button type="submit">Subscribe</button>
                    </form>
                    <p className="FooterRightHeading">KEEP IN TOUCH</p>
                    <div className="Location">
                        <span><i className="fas fa-map-marker-alt"></i></span>
                        <p>{footerData.location}</p>
                    </div>
                    <div className="ContactDetails">
                        <div className="ContactDetail">
                            <span><i className="far fa-envelope"></i></span>
                            <p>{footerData.info_email}</p>
                        </div>
                        <div className="ContactDetail">
                            <span><i className="fas fa-phone"></i></span>
                            <p>{footerData.phone}</p>
                        </div>
                        <div className="ContactDetail">
                            <span><i className="far fa-envelope"></i></span>
                            <p>{footerData.support_email}</p>
                        </div>
                    </div>
                </div>
            </div>
            <p className="Copyright">SOGOVR &copy; 2020</p>
        </div>
    )
}

export default Footer;
