import React, { Fragment } from 'react';
import FaqItem from './Components/FaqItem/FaqItem';
import Header from './Components/OtherHeader/OtherHeader';
import axios from 'axios';
import { useEffect, useState } from 'react';
import Loading from './Components/Loading/Loading';
import ReloadError from './Components/ReloadError/ReloadError';


const Faq = props => {

    const [faq, changeFaq] = useState(null);
    const [loading, changeLoading] = useState(false);
    const [error, changeError] = useState(false);

    const loadFaq = () => {
        changeLoading(true);
        changeError(false);
        axios.get('/api/faq/')
            .then(res => {
                changeLoading(false);
                changeFaq(res.data);
            })
            .catch(() => {
                changeLoading(false);
                changeError(true);
            })
    }

    useEffect(() => {
        loadFaq();
    }, [])

    let faqContent = null;

    if (loading) {
        faqContent = <Loading />
    } else if (faq) {
        faqContent = faq.map(question => (
            <FaqItem title={question.question}
                content={question.answer} />
        ))
    } else if (error) {
        faqContent = <ReloadError />
    }

    return (
        <Fragment>
            <Header pageTitle='FAQ' />
            <div className="Container">
                <h1 className="FaqHeader">Help Center</h1>
                {faqContent}
            </div>
        </Fragment>
    )
}


export default Faq;