import React, { useState } from 'react';
import { propTypes } from 'react-bootstrap/esm/Image';
import './FaqItem.css';


const FaqItem = props => {
    const [open, toggleOpen] = useState(false);

    return (
        <div className="FaqItem">
            <div className="FaqTitle" onClick={() => toggleOpen(!open)} >
                <p>{props.title}</p>
                <span><i className={open?"fas fa-caret-up":"fas fa-caret-down"}></i></span>
            </div>
            <div className="FaqBody" style={{ height: open ? 'fit-content' : 0, padding: open ? 20 : '0 20px' }} 
                dangerouslySetInnerHTML={{__html: props.content}}>
            </div>
        </div>
    )
}

export default FaqItem;