import React, { Fragment } from 'react';
import { useState } from 'react';
import OrderSummaryItem from './Components/OrderSummaryItem/OrderSummaryItem';
import Header from './Components/OtherHeader/OtherHeader';
import { useSelector } from "react-redux";
import Payment from './Payment';


const Checkout = props => {

    const [formData, changeFormData] = useState({
        email: '',
        name: '',
        streetName: '',
        city: '',
        country: '',
        zipCode: '',
        phoneNumber: '',
    });

    const [emailError, changeEmailError] = useState(false);
    const [totoalAmount, changeTotalAmount] = useState(0);

    const isFormField = () => {
        let result = Boolean(formData.email && formData.name && formData.streetName
            && formData.city && formData.country && formData.zipCode
            && formData.phoneNumber && !emailError)

        return result;
    }

    function validateEmail(email) {
        const re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
        return re.test(String(email).toLowerCase());
    }

    const updateEmailField = e => {
        changeFormData({ ...formData, email: e.target.value })
        if (!validateEmail(e.target.value)) {
            changeEmailError(true);
        } else {
            changeEmailError(false);
        }
    }

    const cartProducts = useSelector(state => state.cart.cartProducts);

    let amount = 0;
    for (let product of cartProducts) {
        amount += (product.product_obj.price * product.quantity)
    }
    amount = amount * 100;

    return (
        <Fragment>
            <Header />
            <div className="Container">
                <div className="CheckoutContainer">
                    <div className="CheckoutLeft">
                        <h3>Enter Details</h3>
                        <p>Please fill in the appropriate details below</p>
                        <div className="Input">
                            <label >Email Address <span>*</span></label>
                            {emailError ? <p className="ErrorText">Enter a valid email address</p> : null}
                            <input required type="email"
                                value={formData.email} onChange={updateEmailField}
                                placeholder="sogolivetech@glive.net" />
                        </div>
                        <div className="Input">
                            <label >Name <span>*</span></label>
                            <input required type="text"
                                value={formData.name} onChange={e => changeFormData({ ...formData, name: e.target.value })}
                                placeholder="John Doe" />
                        </div>
                        <div className="Input">
                            <label >Street Name <span>*</span></label>
                            <input required value={formData.streetName} onChange={e => changeFormData({ ...formData, streetName: e.target.value })}
                                type="text" placeholder="1, california" />
                        </div>
                        <div className="InputContainer">
                            <div className="Input">
                                <label >City <span>*</span></label>
                                <input required
                                    value={formData.city} onChange={e => changeFormData({ ...formData, city: e.target.value })}
                                    type="text" placeholder="Lagos" />
                            </div>
                            <div className="Input">
                                <label >Country <span>*</span></label>
                                <input required
                                    value={formData.country} onChange={e => changeFormData({ ...formData, country: e.target.value })}
                                    type="text" placeholder="Nigeria" />
                            </div>
                        </div>
                        <div className="InputContainer">
                            <div className="Input">
                                <label >Zip Code <span>*</span></label>
                                <input required
                                    value={formData.zipCode} onChange={e => changeFormData({ ...formData, zipCode: e.target.value })}
                                    type="text" pattern="\d" placeholder="240222" />
                            </div>
                            <div className="Input">
                                <label >Phone No. <span>*</span></label>
                                <input required
                                    value={formData.phoneNumber} onChange={e => changeFormData({ ...formData, phoneNumber: e.target.value })}
                                    type="text" placeholder="+23481000000" pattern="[0-9]" />
                            </div>
                        </div>
                        <div className="Proceed">
                            {isFormField() ? <Payment
                                name={formData.name}
                                streetName={formData.streetName}
                                city={formData.city}
                                country={formData.country}
                                zipCode={formData.zipCode}
                                phoneNumber={formData.phoneNumber}
                                email={formData.email} amount={amount}
                                className="CheckoutButton" />
                                : <div className="CheckoutButton" style={{
                                    cursor: 'not-allowed'
                                }} >Make Payment</div>}
                        </div>
                    </div>
                    <div className="CheckoutRight">
                        <h5>Order Summary</h5>
                        {cartProducts.map(product => <OrderSummaryItem
                            product={product} key={product.id} />)}
                        {/* <div className="PrevNext">
                            <div className="PrevButton">Previous</div>
                            <div className="NextButton">Next</div>
                        </div> */}
                    </div>
                </div>
            </div>
        </Fragment>
    )
}

export default Checkout;