import * as type from '../types';

export function getCartQuantity(/* pass payload */) {
    return {
        type: type.GET_CART_QUANTITY,
        payload: null
    }
}

export function updateCartQuantity(addOrSub) {
    return {
        type: type.UPDATE_CART_QUANTITY,
        payload: addOrSub
    }
}

export function getCartProducts(cartId) {
    return {
        type: type.GET_CART_PRODUCTS_REQUESTED,
        cartId: cartId
    }
}

export function updateCartId(cartId) {
    return {
        type: type.UPDATE_CART_ID,
        cartId: cartId
    }
}
