import React from 'react';
import './CartItem.css';
import { useState } from 'react';
import { useEffect } from 'react';
import axios from 'axios';
import { useDispatch, useSelector } from "react-redux";
import { getCartProducts, updateCartQuantity } from '../../store/actions/cart';


const CartItem = props => {

    const dispatch = useDispatch();

    const cartId = useSelector(state => state.cart.cartId);

    const [quantity, changeQuantity] = useState(1);

    useEffect(() => {
        changeQuantity(props.data.quantity);
    }, [props.data.quantity])

    const updateProductQuantity = (id, qty) => {
        axios.patch('/api/cart-products/'+id+'/', { quantity: qty })
            .then(res => {
                dispatch(getCartProducts(cartId));
            })
    }

    const updateQuantity = (e, id) => {
        if (e.target.value > 0) {
            changeQuantity(parseInt(e.target.value));
            updateProductQuantity(id, e.target.value);
        }
    }

    const deleteCartProduct = id => {
        axios.delete('/api/cart-products/'+id+'/')
            .then(res => dispatch(getCartProducts()));
    }

    return (
        <div className="CartItemContainer CartCard">
            <div className="CartFlex">
                <div className="CartItem CartItemFlex">
                    <img src={props.data.product_obj.image} alt="vr img" />
                    <p>{props.data.product_obj.title}</p>
                </div>
                <p className="CartUnitPrice">${props.data.product_obj.price}</p>
                <p className="CartQuantity">
                    <input type="number" value={quantity}
                        onChange={e => updateQuantity(e, props.data.id)} />
                </p>
                <p className="CartSubTotal">${props.data.product_obj.price * quantity}</p>
            </div>
            <div className="Remove" >
                <div onClick={() => deleteCartProduct(props.data.id)}>
                    <i className="fas fa-trash"></i>
                    <span>Remove</span>
                </div>
            </div>
        </div>
    )
}

export default CartItem;
