import * as type from '../types';

const initialState = {
    quantity: 0,
    loading: false,
    users: [],
    error: null,
    cartProducts: [],
    cartId: null
}

export default (state=initialState, action) => {
    switch (action.type) {
        case type.GET_CART_QUANTITY:
            return state
        // case type.INCREASE_CART_QUANTITY:
            
        case type.UPDATE_CART_QUANTITY:
            return {
                ...state,
                quantity: state.quantity + action.payload
            }

        case type.GET_USER_REQUESTED:
            return {
                ...state,
                loading: true
            }
        case type.GET_USER_SUCCESS:
            return {
                ...state,
                loading: false,
                users: action.users
            }
        case type.GET_USER_FAILED:
            return {
                ...state,
                loading: false,
                error: action.error.message
            }
        case type.GET_CART_PRODUCTS_REQUESTED:
            return {
                ...state,
                loading: true
            }
        case type.GET_CART_PRODUCTS_SUCCESS:
            return {
                ...state,
                loading: false,
                cartProducts: action.cartProducts,
                quantity: action.cartProducts.length
            }
        case type.GET_CART_PRODUCTS_FAILED:
            return {
                ...state,
                loading: false,
                error: action.error.message
            }
        case type.UPDATE_CART_ID:
            return {
                ...state,
                cartId: parseInt(action.cartId)
            }
        default: return state;
    }
}