import React, { Fragment, useState, useRef, useEffect } from 'react';
import Header from './Components/Header/Header';
import Features from './Components/Features/Features';
import DownloadVr from './Components/DownloadVr/DownloadVr';
import Events from './Components/Events/Events';
import UserComments from './Components/UserComments/UserComments';
import Subscribe from './Components/Subscribe/Subscribe';
import ContactUs from './Components/ContactUs/ContactUs';
import Slider from './Components/Slider/Slider';
import { BASE_URL } from "./CONFIG";
import axios from 'axios';
import Loading from './Components/Loading/Loading';
import ReloadError from './Components/ReloadError/ReloadError';


const Landing = props => {

    const [animateTop, changeAnimateTop] = useState(false);
    const [animateBottom, changeAnimateBottom] = useState(false);
    const [downloadVrAnimate, changeDownloadVrAnimate] = useState(false);
    const [subscribeAnimate, changeSubscribeAnimate] = useState(false);
    const [contactUsAnimate, changeContactUsAnimate] = useState(false);

    const downloadVrRef = useRef();
    const featureTopRef = useRef(null);
    const featureBottomRef = useRef(null);
    const subscribeRef = useRef();
    const contactUsRef = useRef();

    const [landing, changeLanding] = useState(null);
    const [loading, changeLoading] = useState(false);
    const [error, changeError] = useState(false);

    const loadLanding = () => {
        changeLoading(true);
        changeError(false);
        axios.get('/api/landing/')
            .then(res => {
                changeLoading(false);
                changeLanding(res.data);
            })
            .catch(() => {
                changeLoading(false);
                changeError(true);
            })
    }

    useEffect(() => {
        window.onscroll = e => {
            if (downloadVrRef.current) {
                if (!downloadVrAnimate && (downloadVrRef.current.getBoundingClientRect().top < window.screen.height)) {
                    changeDownloadVrAnimate(true);
                }
            }
            if (featureTopRef.current) {
                if (!animateTop && (featureTopRef.current.getBoundingClientRect().top < window.screen.height)) {
                    changeAnimateTop(true);
                }
            }
            if (featureBottomRef.current) {
                if (!animateBottom && (featureBottomRef.current.getBoundingClientRect().top < window.screen.height)) {
                    changeAnimateBottom(true);
                }
            }
            if (subscribeRef.current) {
                if (!subscribeAnimate && (subscribeRef.current.getBoundingClientRect().top < window.screen.height)) {
                    changeSubscribeAnimate(true);
                }
            }
            if (contactUsRef.current) {
                if (!contactUsAnimate && (contactUsRef.current.getBoundingClientRect().top < window.screen.height)) {
                    changeContactUsAnimate(true);
                }
            }
        }
        loadLanding();
    }, [])

    let landingContent = null;
    if (loading) {
        landingContent = <Loading />
    }
    else if (landing) {
        landingContent = (
            <Fragment>
                <Header data={{
                    title: landing.landing.header_title,
                    text: landing.landing.header_text,
                    image: landing.landing.header_image,
                    available_date: landing.landing.available_date,
                    playstore: landing.landing.playstore,
                    appstore: landing.landing.appstore
                }} />
                <Features
                    data={{
                        features_header1: landing.landing.features_header1,
                        smartphone_text: landing.landing.smartphone_text,
                        seamless_text: landing.landing.seamless_text,
                        high_resolution_text: landing.landing.high_resolution_text,
                        all_vr_text: landing.landing.all_vr_text,
                        features_image1: landing.landing.features_image1,
                        features_header2: landing.landing.features_header2,
                        features_image2: landing.landing.features_image2,
                        features_text2: landing.landing.features_text2,
                        features_video: landing.landing.features_video,
                    }}
                    featureTopRef={featureTopRef}
                    featureBottomRef={featureBottomRef}
                    animateTop={animateTop} animateBottom={animateBottom} />
                <DownloadVr
                    data={{
                        heading: landing.landing.dark_banner_heading,
                        text: landing.landing.dark_banner_text,
                        image: landing.landing.dark_banner_image,
                        appstore: landing.landing.appstore_url,
                        playstore: landing.landing.playstore_url,
                    }}
                    animate={downloadVrAnimate}
                    animateRef={downloadVrRef} />
                <Events data={landing.events} />
                {/* <UserComments />
                <Slider /> */}
                <Subscribe
                    animate={subscribeAnimate} animateRef={subscribeRef} />
                <ContactUs image={landing.landing.contact_us_image}
                    animate={contactUsAnimate} animateRef={contactUsRef} />
                {/* <h1>Coming Soon</h1> */}
            </Fragment>
        )
    }
    else if (error) {
        landingContent = <ReloadError />
    }
    return (
        landingContent
    )
}

export default Landing;