import React, { Fragment, useState, useEffect } from 'react';
import Header from './Components/OtherHeader/OtherHeader';
import axios from 'axios';
import Loading from './Components/Loading/Loading';
import ReloadError from './Components/ReloadError/ReloadError';


const Privacy = props => {

    const [privacy, changePrivacy] = useState(null);
    const [loading, changeLoading] = useState(false);
    const [error, changeError] = useState(false);

    const loadPrivacy = () => {
        changeLoading(true);
        changeError(false);
        axios.get('/api/privacy/')
            .then(res => {
                changeLoading(false);
                changePrivacy(res.data);
            })
            .catch(() => {
                changeLoading(false);
                changeError(true);
            })
    }

    useEffect(() => {
        loadPrivacy();
    }, [])

    let privacyContent = null;

    if (loading) {
        privacyContent = <Loading />
    } else if (privacy) {
        privacyContent = privacy.private_policy
    } else if (error) {
        privacyContent = <ReloadError />
    }

    return (
        <Fragment>
            <Header pageTitle="Privacy" />
            <div className="Container" dangerouslySetInnerHTML={{ __html: privacyContent }}></div>
        </Fragment>
    )
}

export default Privacy;
