import React from 'react';
import './OrderSummaryItem.css';
import VrImg from '../../assets/images/shop_vr.jpeg';


const OrderSummaryItem = props => (
    <div className="OrderSummaryItem">
        <img src={props.product.product_obj.image} alt="vr image" />
        <p>
            {props.product.product_obj.title}
        </p>
        <div className="OrderSummaryKeyValue">
            <div className="OrderSummaryKey">UNIT PRICE</div>
            <div className="OrderSummaryValue">${props.product.product_obj.price * props.product.quantity}</div>
        </div>
        <div className="OrderSummaryKeyValue">
            <div className="OrderSummaryKey">QUANTITY</div>
            <div className="OrderSummaryValue">{props.product.quantity}</div>
        </div>
    </div>
)

export default OrderSummaryItem;