import React, { Fragment, useState, useEffect } from 'react';
import Loading from './Components/Loading/Loading';
import Header from './Components/OtherHeader/OtherHeader';
import ReloadError from './Components/ReloadError/ReloadError';
import axios from 'axios';


const About = props => {

    const [about, changeAbout] = useState(null);
    const [loading, changeLoading] = useState(false);
    const [error, changeError] = useState(false);

    const loadAbout = () => {
        changeLoading(true);
        changeError(false);
        axios.get('/api/about/')
            .then(res => {
                changeLoading(false);
                changeAbout(res.data);
            })
            .catch(() => {
                changeLoading(false);
                changeError(true);
            })
    }

    useEffect(() => {
        loadAbout();
    }, [])

    let aboutContent = null;

    if (loading) {
        aboutContent = <Loading />
    } else if (about) {
        aboutContent = about.about
    } else if (error) {
        aboutContent = <ReloadError />
    }

    return (
        <Fragment>
            <Header pageTitle="About" />
            <div className="Container" dangerouslySetInnerHTML={{ __html: aboutContent }}></div>
        </Fragment>
    )
}

export default About;
